import React from "react";
import { Card, Jumbotron } from "../../strapped";
import { posts } from '../../data/posts'

export const Blog = () => {
  return (
    <Jumbotron>
      {posts.map((post) => (
        <Card variant="large" key={post.id}>
          <Card.Title>{post.title}</Card.Title>
          <Card.Text>{post.description}</Card.Text>
          {post.link && <Card.Link href={post.link} text={post.linkText} />}
          <Card.Text>{`${post.date.day} ${post.date.month}, ${post.date.year}`}</Card.Text>
        </Card>
      ))}
    </Jumbotron>
  );
};
