import React, { useMemo } from "react";

const Card = ({ children, variant }) => {
  const maxWidth = useMemo(() => {
    if (variant === "medium") {
      return "36rem";
    } else if (variant === "large") {
      return "54rem";
    }

    // small/default
    return "18rem";
  }, [variant]);

  return (
    <div className="card" style={{ margin: "4px", padding: "16px", maxWidth, minWidth: "18rem" }}>
      {children}
    </div>
  );
};

const Body = ({ children }) => {
  return <div className="card-body"></div>;
};

const TopImage = ({ src, alt }) => {
  return <img className="card-img-top" src={src} alt={alt} />;
};

const Title = ({ children }) => {
  return <h5 className="card-title">{children}</h5>;
};

const Text = ({ children }) => {
  return <p className="card-text">{children}</p>;
};

const Link = ({ href, text }) => {
  return (
    <a href={href} className="card-link">
      {text}
    </a>
  );
};

Card.Body = Body;
Card.TopImage = TopImage;
Card.Title = Title;
Card.Text = Text;
Card.Link = Link;

export { Card };
