import { Container } from '../Container';
import React from 'react';

const Grid = ({children}) => {
    return (
        <Container>
            {children}
        </Container>
    )
}

const Row = ({children}) => {
    return (<div className="row">{children}</div>)
}

const Column = ({children}) => {
    return (<div className="col">{children}</div>)
}

Grid.Column = Column;
Grid.Row = Row;

export { Grid };