import React from "react";
import { Card, Grid, Jumbotron } from "../../strapped";
import "./styles.css";
import { aboutData } from '../../data/about'

export const About = () => {
  return (
    <Jumbotron>
      <Grid>
        <Grid.Row>
          {aboutData.map((data, dataIndex) => (
            <Grid.Column key={dataIndex}>
              <Card variant={data.text.length === 1 ? "small" : "medium"}>
                <Card.Title>{data.title}</Card.Title>
                {data.text.map((text, textIndex) => (
                  <Card.Text key={textIndex}>{text}</Card.Text>
                ))}
              </Card>
            </Grid.Column>
          ))}
        </Grid.Row>
      </Grid>
    </Jumbotron>
  );
};
